/* App.css */

.App {
    text-align: center;
    font-family: 'Fira Mono', monospace;
  }
  
  .App-header {
    background-color: var(--baby-blue);
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: var(--navy-blue);
  }
  
  .App-logo {
    height: 150px;
    margin-bottom: 20px;
  }
  
  .App-buttons {
    display: flex;
    gap: 20px;
  }
  
  .btn {
    font-size: 1.2em;
    padding: 0.5em 1.5em;
    color: var(--white);
    background-color: var(--royal-blue);
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .btn:hover {
    background-color: var(--blue-grotto);
  }
  